:root {
  --gap: 24px;
  --carts: 4;
  --gap-info: 4px;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  object-fit: cover;
  display: block;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  font-size: 12px;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-header {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.form-design {
  width: 100%;
  height: 60px;
  background-color: #0514a2;
  align-items: center;
  display: flex;
}

.search-form {
  width: 500px;
  background: #fff;
  border-radius: 3px;
  align-items: center;
  margin: auto;
  display: flex;
}

form.search-form input[type="text"] {
  width: calc(100% - 40px);
  border: none;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  padding: 10px;
  font-size: 17px;
}

form.search-form button {
  width: 40px;
  color: #000;
  cursor: pointer;
  background: #babfc4;
  border: none;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  margin-left: auto;
  padding: 10px;
  font-size: 17px;
}

form.search-form button:hover {
  background: #828588;
}

.gallery {
  gap: var(--gap);
  background-color: #f8fafb;
  flex-wrap: wrap;
  padding: 74px 15px 0;
  display: flex;
}

.photo-card {
  flex-basis: calc((100% - ((var(--carts) )  - 1) * var(--gap) ) / var(--carts) );
  max-height: 231px;
}

.photo {
  height: 79%;
}

.info {
  width: 100%;
  gap: var(--gap-info);
  padding: 8px 0;
  display: flex;
}

.info-item {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

